//colors
$orange: #da6220;
$cream: #ede8d1;
$white: #ffffff;
$black: #242125;
$grey: #68636d;
$green: #477d14;
$bulma-red: #ff3860;

//Bulma Color Assignment
$body-color: $black;
$primary: $orange;
$success: $green;
$warning: $bulma-red;
$link: $orange;
$danger: $black;
$info: $cream;
$body-background-color: $white;

//Bulma variables
$navbar-height: 6rem;
$navbar-item-img-max-height: 4rem;
$navbar-dropdown-arrow: $cream !important;

.navbar {
	&.has-logo-above {
		flex-direction: column; } }

.navbar-item {
	&.field {
		width: 100%; } }

.navbar-brand {
	justify-content: space-between; }

.navbar-item {
	& .force-center {}
	align-item: center; }

.hero-video video {
	width: 100%; }









